<script>
import Input from "@/components/Input.vue";
import Head from "./../../components/Head.vue";
import SectionTitle from "./../../components/SectionTitle.vue";
import Button from "@/components/Button.vue";
import Section from "@/components/Section.vue";
import Table from "./../../components/Table.vue";
import Select from "@/components/Select.vue";
import ReturnToPage from "@/components/ReturnToPage.vue";
import Loader from "./../../components/Loader.vue";
import { toast } from "vue3-toastify";
import "vue3-toastify/dist/index.css";
import html2canvas from 'html2canvas';
import jsPDF from 'jspdf';/*  */

export default {
  props: ["userData"],
  data() {
    return {
      loader: true,
      view: "list-contracts",
      hasNextPage: undefined,
      itemsPerPage: 10,
      actualPage: 0,
      totalItems: 0,
      totalPages: 0,
      searchTermsFilter: "",
      tableHeaders: [
        "ID",
        "Nmero do contrato",
        "Empresa",
        "Jovem Aprendiz",
        "Matrcula CBO",
      ],
      tableActions: ["document-text"],
      tableContents: [],
      tableContentsFullInfos: [],
      tableContentsFullInfosSelectedUser: [],
      editContract: {
        apprentice_id: "",
        company_id: "",
        cbo_id: "",
        contract_number: undefined
      },
      youngApprenticesSelect: [],
      companySelect: [],
      cbosSelect: [],
      generateContract: false,
      listContractInfos: undefined
    };
  },
  created() {
    this.getNextPageApprentices();
  },
  methods: {
    notify(text, type) {
      toast(text, {
        type:
          type == "info"
            ? "info"
            : type == "warning"
            ? "warning"
            : type == "error"
            ? "error"
            : type == "success"
            ? "success"
            : "default",
      });
    },
    getNextPageApprentices() {
      this.loader = true;

      this.actualPage += 1;

      this.$axios
        .get(
          `/contracts?items_per_page=${this.itemsPerPage}&page=${this.actualPage}&terms_filter=${this.searchTermsFilter}`
        )
        .then((res) => {
          console.log(res);

          const data = res.data.data.data;

          this.tableContentsFullInfos = data;

          var arr = [];

          data.forEach((element) => {
            arr.push({
              id: element.id,
              contract_number: element.contract_number,
              social_reason_company: element.company.company_data.social_reason_company,
              young_apprentice_name: element.young_apprentice.name,
              cbo: element.cbo.code,
            });
          });

          this.tableContents = arr;

          res.data.data.last_page > 1
            ? (this.hasNextPage = true)
            : (this.hasNextPage = false);

          this.totalItems = res.data.data.total;
          this.totalPages = res.data.data.last_page;

          this.loader = false;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    listApprenticesActions(event) {
      if (event.eventType == "document-text") {
        this.loader = true;
        
        this.$axios.get(`contracts/${event.data.id}`)
        .then(res => {
            this.listContractInfos = res.data.data;

            this.generateContract = true;

            this.loader = false;
        })
        .catch((err) => {
            this.notify('Ocorreu um erro e no foi possvel processar o contrato!', 'error');
            this.loader = false;
            return;
        });

        this.view = "download-contracts";
      }
    },
    actionPaginationArrows(event) {
      console.log(event);
      if (event == "previous") {
        this.actualPage -= 2;

        this.getNextPageApprentices();
      }

      if (event == "next") {
        this.getNextPageApprentices();
      }
    },
    actionPaginationPages(event) {
      this.actualPage = event - 1;

      this.getNextPageApprentices();
    },
    getApprenticesFilter() {
      this.actualPage = 0;

      this.getNextPageApprentices();
    },
    editYoungApprenticies() {
      if (this.view == "add-contracts") {
        console.log(this.editCBO);
        if (!this.editCBO.code) {
          return this.notify("Preencha o campo de cdigo e funo!", "error");
        }

        this.loader = true;

        this.$axios
          .post("cbos", this.editCBO)
          .then((res) => {
            this.notify("Empresa criada com sucesso!", "success");

            setTimeout(() => {
              window.location.reload();
            }, 2000);
          })
          .catch((err) => {
            this.notify("Ocorreu um erro durante a criao da empresa!", "error");
            this.loader = false;
          });
      }

      if (this.view == "edit-contracts") {
        this.loader = true;

        this.$axios
          .put(`cbos/${this.tableContentsFullInfosSelectedUser.id}`, this.editCBO)
          .then((res) => {
            this.notify("Dados da empresa atualizados com sucesso!", "success");

            setTimeout(() => {
              window.location.reload();
            }, 2000);
          })
          .catch((err) => {
            this.notify(
              "Ocorreu um erro durante a atualizao de dados da empresa!",
              "error"
            );
          });
      }
    },
    viewAddContracts() {
      this.loader = true;

      this.$axios
        .get("contracts/data/to_contract")
        .then((res) => {
          const data = res.data.data;
          console.log(data);

          this.editContract.apprentice_id = data.young_apprentices[0].id;

          data.young_apprentices.forEach((element) => {
            this.youngApprenticesSelect.push({
              name: element.name,
              value: element.id,
            });
          });

          this.editContract.company_id = data.companies[0].user_id;

          data.companies.forEach((element) => {
            this.companySelect.push({
              name: element.social_reason_company,
              value: element.user_id,
            });
          });

          this.editContract.cbo_id = data.cbos[0].id;

          data.cbos.forEach((element) => {
            this.cbosSelect.push({
              name: element.code,
              value: element.id,
            });
          });

          this.view = "add-contracts";
          this.loader = false;
        })
        .catch((err) => {
          this.notify(
            "Ocorreu um erro ao tentar abrir a tela de cadastro de contratos!",
            "error"
          );
          this.loader = false;
        });
    },
    generateFullContract() {
      this.loader = true;

      this.$axios
        .get(
          `contracts/data/to_make_contract/${this.editContract.apprentice_id}/${this.editContract.company_id}/${this.editContract.cbo_id}`
        )
        .then((res) => {
            this.listContractInfos = res.data.data;

            this.editContract.contract_number = new Date().getTime();

            this.generateContract = true;

            console.log(this.listContractInfos);

            this.loader = false;
        })
        .catch((err) => {
          this.notify("Ocorreu um erro ao gerar o contrato!", "error");
          this.loader = false;
        });
    },
    saveContract() {
        if (this.view == 'download-contracts') {
            /* var prtContent = document.getElementById("generateContract");

      // Print functionality
      var WinPrint = window.open('', '', 'left=0,top=0,width=800,height=900,toolbar=0,scrollbars=0,status=0');
      WinPrint.document.write(prtContent.innerHTML);
      WinPrint.document.close();
      WinPrint.focus();
      WinPrint.print();
      WinPrint.close();

      // Download as PDF functionality
      html2canvas(prtContent).then(canvas => {
        const imgData = canvas.toDataURL('image/png');
        const pdf = new jsPDF('p', 'mm', 'a4');

        // Calculate image dimensions to fit A4 page size
        const imgWidth = 210;
        const pageHeight = 295;
        const imgHeight = canvas.height * imgWidth / canvas.width;
        let heightLeft = imgHeight;
        let position = 0;

        pdf.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight);
        heightLeft -= pageHeight;

        while (heightLeft >= 0) {
          position = heightLeft - imgHeight;
          pdf.addPage();
          pdf.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight);
          heightLeft -= pageHeight;
        }

        pdf.save('documento.pdf');
    }); */

            var prtContent = document.getElementById("generateContract");
            var WinPrint = window.open('', '', 'left=0,top=0,width=800,height=900,toolbar=0,scrollbars=0,status=0');
            WinPrint.document.write(prtContent.innerHTML);
            WinPrint.document.close();
            WinPrint.focus();
            WinPrint.print();
            WinPrint.close();
        }

        if (this.view == 'add-contracts') {
            this.loader = true;
    
            this.$axios.post('contracts', {
                young_apprentice_id: this.editContract.apprentice_id,
                company_id: this.editContract.company_id,
                cbo_id: this.editContract.cbo_id,
                contract_number: this.editContract.contract_number
            })
            .then(res => {
                this.notify('Contrato salvo com sucesso!', 'success');
    
                this.view = 'download-contracts';
                this.loader = false;
            })
            .catch((err) => {
                this.notify('Ocorreu um erro ao tentar salvar o contrato!', 'error');
    
                this.loader = false;
            });
        }
    },
    reload() {
        window.location.reload();
    },
  },
  components: {
    Head,
    SectionTitle,
    Input,
    Button,
    Section,
    Table,
    Select,
    ReturnToPage,
    Loader,
  },
};
</script>

<template>
  <Loader v-if="loader" />

  <section v-if="!loader">
    <Head
      :title="
        view == 'list-contracts'
          ? 'Contratos'
          : view == 'download-contracts'
          ? 'Baixar Contrato'
          : 'Adicionar Contratos'
      "
    />

    <!-- Adicionar ou ver/baixar contratos -->
    <Section v-if="view == 'download-contracts' || view == 'add-contracts'">
      <ReturnToPage
        placeholder="Voltar para a listagem de contratos"
        redirectTo=""
        @clickReturn="
            reload();
          youngApprenticesSelect = [];
          companySelect = [];
          cbosSelect = [];
          generateContract = false;
          view = 'list-contracts';
        "
      ></ReturnToPage>

      <div class="form-divider"></div>

      <Select
        v-if="view == 'add-contracts'"
        label="Selecione o jovem aprendiz"
        :options="youngApprenticesSelect"
        :value="editContract.apprentice_id"
        v-model="editContract.apprentice_id"
      />

      <div class="form-divider"></div>

      <Select
        v-if="view == 'add-contracts'"
        label="Selecione a empresa"
        :options="companySelect"
        :value="editContract.company_id"
        v-model="editContract.company_id"
      />

      <div class="form-divider"></div>

      <Select
        v-if="view == 'add-contracts'"
        label="Selecione a matrcula CBO"
        :options="cbosSelect"
        :value="editContract.cbo_id"
        v-model="editContract.cbo_id"
      />

      <div class="form-divider"></div>

      <Button
        v-if="view == 'add-contracts'"
        type="secondary"
        placeholder="Gerar contrato"
        @buttonPressed="generateFullContract"
      />

      <div class="form-divider"></div>

      <section v-if="generateContract" id="generateContract" class="bg-generated-contract">
        <img
          style="width: 150px; max-width: 98%; text-align: center"
          src="https://painel.iaq.org.br/wp-content/uploads/2023/10/LOGO-IAQ-07-768x704-1.png"
          alt=""
          width="150"
          height="150"
        />
        <p style="text-align: center; margin-top: 30px">
          <strong><u>CONTRATO DE APRENDIZAGEM</u></strong>
        </p>
        <p style="text-align: center; margin-top: 10px;">N. Contrato: {{ editContract.contract_number }}</p>

        <table style="height: 280px; margin-top: 30px;" width="1087">
          <tbody>
            <tr>
              <td style="text-align: center"><strong>QUALIFICADORA</strong></td>
            </tr>
            <tr>
              <td style="text-align: left; line-height: 25px;">
                Razo Social: INSTITUTO APRENDER DE QUALIFICAO <br />
                Endereo: SGAS 604 LOTE 27 AVENIDA L2 SUL - ASA SUL - 1 ANDAR SALA 110 -
                CEP: 70.200-640 Cidade: BRASILIA - DF- <br />
                CNPJ: 12.388.176/0001-41 <br />
                Tel.:(061) - 99828-0686 / (61) 99829-0398 <br />
                Representante Legal: ADILSON MARIZ DE MORAES - Presidente
              </td>
            </tr>
          </tbody>
        </table>
        <table style="height: 280px; margin-top: 0px;" width="1088">
          <tbody>
            <tr>
              <td style="text-align: center"><strong>EMPREGADORA</strong></td>
            </tr>
            <tr>
              <td style="text-align: left; line-height: 25px;">
                Razo Social: {{ listContractInfos.company.company_data.social_reason_company }} <br />
                Endereo: {{ listContractInfos.company.company_data.address_company }} - {{ listContractInfos.company.company_data.state_city_company }} - {{ listContractInfos.company.company_data.address_zipcode_company }} <br />
                CNPJ: {{ listContractInfos.company.company_data.cnpj_company }} <br />
                Tel.: {{ listContractInfos.company.company_data.phone_company }} / {{ listContractInfos.company.company_data.phone_youth_supervisor }} <br />
                Representante Legal: {{ listContractInfos.company.company_data.name_legal_representative }} <br />
                Supervisor do Aprendiz: {{ listContractInfos.company.company_data.name_youth_supervisor }}
              </td>
            </tr>
          </tbody>
        </table>
        &nbsp;
        <table
          style="height: 280px; margin-top: 0px; margin-bottom: 40px"
          width="1087"
        >
          <tbody>
            <tr>
              <td style="text-align: center"><strong>APRENDIZ</strong></td>
            </tr>
            <tr>
              <td style="text-align: left; line-height: 25px;">
                Nome: {{ listContractInfos.young_apprentice.name }} <br />
                Data nascimento: {{ listContractInfos.young_apprentice.young_apprentice_data.date_of_birth }} <br />
                RG: {{ listContractInfos.young_apprentice.young_apprentice_data.document_rg }} <br />
                CPF: {{ listContractInfos.young_apprentice.young_apprentice_data.document_cpf }} <br />
                CTPS: {{ listContractInfos.young_apprentice.young_apprentice_data.work_card }} SRIE: {{ listContractInfos.young_apprentice.young_apprentice_data.series_work_card }} <br />
                FILIAO: {{ listContractInfos.young_apprentice.young_apprentice_data.mother_name }} <br />
                Endereo: {{ listContractInfos.young_apprentice.young_apprentice_data.address }} - {{ listContractInfos.young_apprentice.young_apprentice_data.address_city }} - {{ listContractInfos.young_apprentice.young_apprentice_data.address_zipcode }} <br />
                TEL: (61) 99561-1501  <br />
                Matrcula no Curso: <strong> </strong>.
              </td>
            </tr>
          </tbody>
        </table>
        <p style="text-align: left; margin-bottom: 20px;">
            &nbsp; Pelo presente instrumento de contrato que entre si fazem, as partes acima
            qualificadas que resolvem firmar e estabelecer as seguintes clusulas que regero
            as condies contratuais, que se comprometem a cumprir at o seu termo final, com
            efeito de ttulo executivo extrajudicial, para todos os fins e efeitos de direito.
        </p>

        <p style="text-align: left; margin-bottom: 20px;">
            <strong>Clusula Primeira</strong>  A EMPREGADORA admite, como seu empregado, o
            APRENDIZ acima qualificado, conforme dispe o Artigo 428 da CLT,
            <strong>comprometendo-se a proporcionar-lhe matrcula e frequncia</strong> no
            curso de aprendizagem..
        </p>

        <p style="text-align: left; margin-bottom: 20px;">
            <strong>Clusula Segunda</strong>  A aprendizagem desenvolver-se- em duas fases
        concomitantes, sendo a terica na entidade <strong>QUALIFICADORA </strong>e a
        prtica profissional no estabelecimento do EMPREGADORA.
        </p>

        <p style="text-align: left; margin-bottom: 20px;">
            <strong>Clusula Terceira</strong>  A vigncia do presente contrato ser de 24
        meses, ininterruptos, compreendendo o perodo de 13/05/2024 a 12/05/2026. O
        trmino est vinculado ao trmino do curso, portanto, o contrato no  passvel de
        prorrogao. A Formao Profissional se dar da seguinte maneira:
        </p>

        <p style="text-align: left; margin-bottom: 20px;">
            <u>Fase terica</u> (400 horas): sendo de 04h (quatro) horas semanais, cumpridas
        no Instituto Aprender de Qualificao  IAQ, toda 3 feira das 14h00 s 18h00,
        sendo que, no perodo de 20/05/2024 a 31/05/2024, sero cumpridas 40h de aulas
        tericas, exclusivamente na Qualificadora. A atuao do APRENDIZ ser na funo de
        acordo com o <strong>CBO {{ listContractInfos.cbo.code }}</strong>, abrangendo as seguintes atividades:
        {{ listContractInfos.cbo.description }}
        </p>

        <p style="text-align: left; margin-bottom: 20px;">
            <u>Fase Prtica:</u> a jornada de trabalho do APRENDIZ ser de
        <strong>04 </strong>horas dirias, perfazendo o total de 16 horas semanais, de
        prtica profissional a serem cumpridas na EMPREGADORA, vedadas a prorrogao e a
        compensao da jornada, inclusive nas hipteses previstas nos incisos I e II do
        artigo 413 da CLT. O horrio de trabalho do jovem na EMPREGADORA ser de 13h30 s
        17h30, de Segunda  Sexta, a exceo do dia de curso.
        </p>

        <p style="text-align: left; margin-bottom: 20px;">
            <u>Total do Programa:</u> 04 horas dirias, de Segunda  Sexta, totalizando 20
        horas semanais. As horas de formao terica so consideradas como horas
        efetivamente trabalhadas e integram para todos os fins, a jornada de trabalho do
        aprendiz.
        </p>

        <p style="text-align: left; margin-bottom: 20px;">
            <strong>Clusula Quarta  </strong>A QUALIFICADORA obriga-se a:
        <ol style="margin-left: 20px;">
          <li>
            Executar os Programas de Aprendizagem, ministrando os contedos tericos,
            metodologicamente organizados conforme Calendrio Pedaggico anexado ao
            presente Contrato, garantindo a articulao e a complementaridade entre a
            aprendizagem terica e prtica, acompanhando a execuo das atividades
            prticas desenvolvidas no mbito da EMPREGADORA;
          </li>
          <li>
            Acompanhar e monitorar o desenvolvimento do Programa de Aprendizagem, mantendo
            mecanismos de controle da frequncia e aproveitamento do APRENDIZ nas
            atividades tericas e prticas, de forma a garantir que as atividades prticas
            estejam em conformidade com o Programa de Aprendizagem previamente traado;
          </li>
          <li>
            Acompanhar mensalmente a frequncia do APRENDIZ s aulas do ensino regular de
            educao;
          </li>
          <li>
            Conceder ao APRENDIZ Certificado de Qualificao Profissional de concluso
            regular e aproveitamento do curso de aprendizagem terica, desde que o
            APRENDIZ tenha participado de forma ativa, com presena em pelo menos 75% das
            aulas do Programa de Aprendizagem.
          </li>
        </ol>
        </p>

        <p style="text-align: left; margin-bottom: 20px;">
            <strong>Clusula Quinta  </strong>A EMPREGADORA obriga-se a<strong>:</strong>
        <ol style="margin-left: 20px;">
          <li>
            Registrar na Carteira de Trabalho e Previdncia Social do APRENDIZ, a vigncia
            do presente CONTRATO DE APRENDIZAGEM;
          </li>
          <li>
            Conceder frias ao APRENDIZ atendendo s necessidades da Empresa, porm em
            conformidade com as frias escolares e devero ser comunicadas com
            antecedncia  entidade QUALIFICADORA e do APRENDIZ seguindo as normas
            pertinentes previstas na CLT.
          </li>
          <li>
            Remunerar o Aprendiz, mensalmente, com base no salrio-mnimo hora, vigente,
            conforme previsto nos termos do pargrafo 2 do Artigo 428 da CLT, salvo
            condio mais favorvel, conveno ou acordo coletivo da categoria. Sendo, no
            caso, a remunerao corresponde ao valor de
            <strong>R$ 6,42 (Seis Reais e Quarenta e dois Centavos)</strong>.
          </li>
          <li>
            Fornecer vale-transporte para a locomoo do APRENDIZ entre sua residncia e a
            entidade qualificadora, e entre sua residncia e a Empresa, concomitantemente
            com outros benefcios proporcionados pela EMPREGADORA a seus demais
            funcionrios por fora de instrumento coletivo de trabalho da categoria,
            quando houver.
          </li>
          <li>
            Comunicar, periodicamente,  QUALIFICADORA as ausncias injustificadas,
            dificuldade de adaptao e desempenho insuficiente do APRENDIZ no exerccio da
            funo/atividade, bem como qualquer outra ocorrncia considerada grave e
            prejudicial ao bom desempenho e ao cumprimento do presente Contrato;
          </li>
          <li>
            Informar ao Instituto Aprender de Qualificao - IAQ, os desligamentos mensais
            ocorridos, at o dia 15 de cada ms para evitar cobrana de taxa
            administrativa no ms subsequente. As Taxas administrativas referentes aos
            desligamentos informados aps o dia 15 de cada ms sero cobradas em sua
            totalidade no ms subsequente.
          </li>
          <li>
            Garantir ao APRENDIZ todos os direitos trabalhistas e previdnciarios
            previstos por lei.
          </li>
          <li>
            Proporcionar ao APRENDIZ formao tcnico-profissional metdica, por meio de
            atividades prticas correlacionadas com os contedos tericos previstos no
            programa de aprendizagem, em ambiente adequado, que apresente condies de
            segurana e sade, em conformidade com os requisitos do Artigo 405 da CLT e
            das Normas Regulamentadoras relativas  Segurana e Medicina
          </li>
          <li>
            Proporcionar ao APRENDIZ qualificao metdica, liberando e estimulando o
            APRENDIZ para o cumprimento do curso teorico no Instituto Aprender de
            Qualificao  IAQ, semanalmente, realizando todas as atividades.
          </li>
          <li>
            Designar profissional com delegao de competncia para acompanhar as
            atividades desenvolvidas pelo APRENDIZ, bem como para ser o interlocutor da
            EMPREGADORA junto ao Instituto Aprender de Qualificao  IAQ.
          </li>
        </ol>
        </p>

        <p style="text-align: left; margin-bottom: 20px;">
            <strong>Clusula Sexta </strong> A EMPREGADORA declara conhecer toda legislao
        pertinente ao objeto do presente contrato, bem como se compromete a cumprir os
        dispositivos legais preconizados nos artigos 428 a 433 da Consolidao das Leis do
        Trabalho, com a alterao dada pela Lei n 10.097/00, como tambm o Decreto n
        6.481 de 2008.
        </p>

        <p style="text-align: left; margin-bottom: 20px;">
            <strong>Clusula Stima</strong>  Sempre que o APRENDIZ deixar de comparecer 
        entidade qualificadora, durante a fase escolar da aprendizagem, ou ao
        estabelecimento da EMPREGADORA durante o perodo de prtica profissional, sem
        justificativa fundamentada, perder o salrio dos dias faltosos.
        </p>

        <p style="text-align: left; margin-bottom: 20px;">
            <strong>Clusula Oitava</strong>  Durante o perodo de recesso escolar, o
        APRENDIZ poder ser convocado(a) pela EMPREGADORA para prestao de servios em
        seu estabelecimento, observando-se a Consolidao das Leis do Trabalho no que
        concerne a frias e limites de trabalho dirio.
        </p>

        <p style="text-align: left; margin-bottom: 20px;">
            <strong>Paragrafo nico</strong>  No caso da empresa convocar o aprendiz, durante
        o recesso escolar, quando no haver carga horria terica, para exercer somente
        atividades prticas no estabelecimento, o limite mximo da jornada diria ser de
        6 (seis) horas sem prejuzo  sua remunerao.
        </p>

        <p style="text-align: left; margin-bottom: 20px;">
            <strong>Clusula Nona  </strong>So de exclusiva responsabilidade da EMPREGADORA
        as obrigaes decorrentes das leis trabalhistas, previdencirias, tributrias, ou
        quaisquer outras, por mais especiais que sejam, com relao ao
        <strong>APRENDIZ</strong>.
        </p>

        <p style="text-align: left; margin-bottom: 20px;">
            <strong>Clusula Dcima </strong> O APRENDIZ obriga-se a:
        <ol style="margin-left: 20px">
          <li>
            Executar, com zelo e diligncia, as tarefas necessrias ao cumprimento das
            atividades desenvolvidas na EMPREGADORA, na QUALIFICADORA e sua formao
            profissional;
          </li>
          <li>
            Cumprir, com exatido, a jornada de atividades tericas e prticas
            desenvolvidas para o bom desempenho do Programa de Aprendizagem , conforme
            descrito na Clusula Terceira;
          </li>
          <li>
            Participar regularmente das aulas e demais atos promovidos pela Qualificadora,
            bem como a cumprir o Regimento e as disposies disciplinares previstas no
            Programa de Aprendizagem;
          </li>
          <li>
            Frequentar as aulas do ensino regular de educao curricular a que estiver
            matriculado, apresentando documento comprobatrio, sempre que solicitado;
          </li>
          <li>
            Cumprir a totalidade da jornada semanal de trabalho na EMPREGADORA quando de
            folgas de aulas tericas.
          </li>
        </ol>
        </p>

        <p style="text-align: left; margin-bottom: 20px;">
            <strong>Clusula Dcima Primeira  </strong>A LGPD (Lei Geral de proteo de
        dados) O (a) APRENDIZ est ciente do tratamento de seus Dados Pessoais realizado
        pela EMPREGADORA para finalidades relacionadas  relao empregatcia. Dados
        Pessoais significam as informaes relacionadas a pessoa natural identificada ou
        identificvel, nos termos da Lei n 13.709/2018 (Lei Geral de Proteo de Dados
        Pessoais  LGPD). Pargrafo Primeiro: O(a) APRENDIZ reconhece que a EMPREGADORA
        poder tratar seus Dados Pessoais, independentemente de seu consentimento: a) Para
        o cumprimento de obrigao legal ou regulatria imposta  EMPREGADORA ou a
        terceiro; b) Quando necessrio para a execuo do presente contrato de trabalho;
        c) Para o exerccio regular de direitos da EMPREGADORA em processo judicial,
        administrativo ou arbitral; d) Para a proteo da vida ou da incolumidade fsica
        do(a) APRENDIZ ou de terceiro; e/ou e) Quando necessrio para atender aos
        interesses legtimos da CONTRATANTE ou de terceiro, observando as restries
        estabelecidas pela legislao aplicvel. Pargrafo Segundo: O(a) APRENDIZ tem
        direito a requerer da EMPREGADORA, a qualquer momento e mediante requisio: a)
        Confirmao da atividade de tratamento de Dados Pessoais; b) Acesso aos Dados
        Pessoais; c) Correo de Dados Pessoais incompletos, inexatos ou desatualizados;
        d) Anonimizao, bloqueio ou eliminao de Dados Pessoais desnecessrios,
        excessivos ou tratados em desconformidade com o disposto na legislao brasileira;
        e) Portabilidade de Dados Pessoais, conforme aplicvel nos termos da legislao
        brasileira; f) Eliminao dos Dados Pessoais tratados com o consentimento do o(a)
        APRENDIZ, observando as restries da legislao brasileira; g) Informao das
        entidades pblicas e privadas com as quais a EMPREGADORA realizou uso
        compartilhado de Dados Pessoais; e h) Informao sobre a possibilidade de no
        fornecer consentimento e sobre as consequncias da negativa, assim como a
        possibilidade de revogao do consentimento. Pargrafo Terceiro:  possvel que
        haja circunstncias que no autorizem  EMPREGADORA dar atendimento ao exerccio
        de algumas das prerrogativas, tais como as previstas no Pargrafo Primeiro acima,
        ou quando o fornecimento das informaes puder revelar algum segredo de negcio da
        EMPREGADORA. Pargrafo Quarto: O (a) APRENDIZ reconhece que a EMPREGADORA pode
        transferir seus Dados Pessoais dentro ou para fora do territrio brasileiro,
        independentemente de seu consentimento, conforme permitido pela legislao
        aplicvel. Pargrafo Quinto: O tratamento de Dados Pessoais do (a) APRENDIZ dever
        ser realizado de acordo com e conforme informado pelo presente contrato de
        trabalho, pela Poltica de Privacidade da EMPREGADORA e a legislao aplicvel.
        </p>

        <p style="text-align: left; margin-bottom: 20px;">
            <strong>Clusula Dcima Segunda</strong>  No exerccio deste contrato de
        trabalho, o (a) APRENDIZ poder tratar Dados Pessoais de outros empregados e/ou de
        terceiros, na forma da LGPD. Pargrafo Primeiro: O(A) APRENDIZ, na hiptese de
        tratar dados pessoais para o desempenho de suas funes, compromete-se a faz-lo
        nos estritos termos da legislao e regulamentao aplicveis (incluindo, mas no
        se limitando, s disposies da LGPD e eventuais regulamentaes a serem
        proferidas pela Autoridade Nacional de Proteo de Dados), das polticas e
        diretrizes internas estabelecidas pela EMPREGADORA, observadas as medidas de
        privacidade e segurana de Dados Pessoais informadas pela
        <b>EMPREGADORA.</b>
        Pargrafo Segundo: O (A) APRENDIZ declara que est ciente das prticas de Proteo
        de Dados estabelecidas pela EMPREGADORA e se compromete a cumpri-las. &nbsp;
        Pargrafo Terceiro: O (A) APRENDIZ dever comunicar imediatamente  EMPREGADORA
        sobre qualquer evento que possa configurar, de forma potencial ou efetiva,
        violao  LGPD e/ou s polticas e diretrizes internas estabelecidas pela
        EMPREGADORA envolvendo Dados Pessoais, e se comprometendo a auxiliar a EMPREGADORA
        na apurao, investigao e/ou verificao de tais violaes, incluindo a adoo
        de medidas para san-las, conforme instrudo pela EMPREGADORA. &nbsp; Pargrafo
        Quarto: O (A) APRENDIZ tem cincia que o descumprimento das obrigaes previstas
        nesta clusula poder resultar na aplicao de sanes disciplinares por parte da
        EMPREGADORA, sem prejuzo da adoo das medidas judiciais cabveis, nos termos da
        legislao e regulamentao vigentes. &nbsp;
        </p>

        <p style="text-align: left; margin-bottom: 20px;">
            <strong>Clusula Dcima Terceira - </strong>So hipteses de resciso de contrato
        de aprendiz, conforme dispe o Artigo 433 da CLT, o Contrato de Aprendizagem
        extinguir-se- no seu trmino ou quando o APRENDIZ completar 24 (vinte e quatro)
        anos, ressalvada a hiptese prevista no pargrafo 5. do Artigo 428 da CLT ou,
        antecipadamente, nas seguintes hipteses: &nbsp;
        <ol style="margin-left: 20px;">
          <li>
            a) desempenho insuficiente ou inadaptao do APRENDIZ comprovado em laudo de
            avaliao elaborado pela entidade qualificadora, salvo para o APRENDIZ com
            deficincia quando desprovido de recursos de acessibilidade, de tecnologias
            assistivas e de apoio necessrio ao desempenho de suas atividades (a 433 da
            CLT);
          </li>
          <li>b) falta disciplinar grave (art. 482 da CLT);</li>
          <li>c) ausncia injustificada  escola que implique perda do ano letivo;</li>
          <li>d) a pedido do aprendiz.</li>
        </ol>
        &nbsp;
        </p>

        <p style="text-align: left; margin-bottom: 20px;">
            <strong>Clusula Dcima Quarta - </strong>As partes elegem o Foro da Circunscrio
        Judiciria de Brasilia/DF, como o nico competente para dirimir quaisquer dvidas
        decorrentes deste Contrato. Assim, aceitas pelas partes as condies ora
        estabelecidas, na livre manifestao de suas vontades, assinam o presente
        instrumento de Contrato de Aprendizagem em 03 (trs) vias, de igual teor e forma,
        na presena das testemunhas abaixo nomeadas e qualificadas, para que surta os
        efeitos legais. &nbsp;
        </p>


        <p style="text-align: center; margin: 15px 0px">Braslia, 13/05/2024.</p>
        &nbsp;
